export const STRING_TO_URL_CONVERT = (string) => {
    try {
        var addString = string;
        if (string && string && string.includes("http")) {
            const urls = string.match(/(((ftp|https?):\/\/)[\-\w@:%_\+.~#?,&\/\/=]+)/g);
            if (urls) {
                urls.forEach((url) => {
                    addString = addString.replace(url, `<a target="_blank" href="${url}">${url}</a>`);
                });
                return addString;
            }
        }
        else return addString;
    }
    catch (ex) {
        return string;
    }
}

export const heightLightTagHas = (string, hashTagStyle) => {
    try {
        var addString = string;
        if (string && string && string.includes("#")) {
            const urls = string.match(/(#[\-\w@:%_\+.~#?,&\/\/=]+)/g);
            if (urls) {
                urls.forEach((url) => {
                    addString = addString.replace(url, `<span key={${uuidv4()}} class={${hashTagStyle}} style={{ color: hashTagStyle, fontWeight: "bold" }}>${url}</span>`);
                });
                return addString;
            }
        }
        else return addString;
    }
    catch (ex) {
        return string;
    }
}

const FETCH_LINK = (fetchUrl, postId, dataSrc, type) => GET_INSTAGRAM_IMAGE(fetchUrl).then((response) => {
    let html = response.data;
    if (html) {
        var regex = /_sharedData = ({.*);<\/script>/m,
            json = JSON.parse(regex.exec(html)[1]);
        if (typeof json.entry_data.PostPage[0].graphql.shortcode_media.edge_sidecar_to_children !== 'undefined') {
            if (type == 'video') {
                const videoPath = json.entry_data.PostPage[0].graphql.shortcode_media.edge_sidecar_to_children.edges.filter((item) => item.node.shortcode == postId[4])[0].node.video_url
                return videoPath;
            }
            else {
                const imagePath = json.entry_data.PostPage[0].graphql.shortcode_media.edge_sidecar_to_children.edges.filter((item) => item.node.shortcode == postId[4])[0].node.display_url
                return imagePath;
            }

        }
        else if (json.entry_data.PostPage[0].graphql.shortcode_media.__typename === 'GraphVideo') {

            if (json.entry_data.PostPage[0].graphql.shortcode_media.is_video) {
                return { video_url: json.entry_data.PostPage[0].graphql.shortcode_media.video_url, display_url: json.entry_data.PostPage[0].graphql.shortcode_media.display_url }
            }
        }
        else {
            return json.entry_data.PostPage[0].graphql.shortcode_media.display_url;
        }
    }

}).catch((error) => {
    if (dataSrc.includes("cdn.taggbox.com")) return NO_CAMERA_IMAGE;
    else return NO_CAMERA_IMAGE;
})


export const bytesToSize = (bytes) => {
    var sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
    if (bytes == 0) return '0 Byte';
    var i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)));
    return { size: Math.round(bytes / Math.pow(1024, i), 2), sizeText: sizes[i] }

}


export const IMG_LOAD_AFTER_COMPLETE_LAYOUT = async (e, callBack) => {
    e.persist()
    // e.target.src = "https://media.geeksforgeeks.org/wp-content/uploads/20200617121258/gfg-image2-300x177.png"
    const network = e.target.getAttribute("data-network")
    const wallId = e.target.getAttribute("data-wall-id");
    const postId = e.target.getAttribute("data-item-id");
    const UserDetail = store.getState().appData.wall.UserDetail;
    const ownerId = store.getState().appData.wall.User.id;
    let image_update = '';
    let video_update = '';
    if (network) {
        axios.post(`${EXPIRE_POST_UPDATESTATUS_NODE}`, {
            wallId: wallId,
            postId: postId,
            table_name: UserDetail.db_table,
            ownerId
        }).then(({ data }) => {
            if (data.code == 200) {
                e.target.src = data.media;
                e.target.attributes["data-load"].value = "1";
                callBack(e)
            }
            else {
                e.target.src = NO_CAMERA_IMAGE;
                e.target.style = "display:block;min-height:150px;width:80px!important;height:100%;margin:0px auto;object-fit: contain;";
                callBack(e)
            }
        })
            .catch((error) => {
                e.target.src = NO_CAMERA_IMAGE;
                e.target.style = "display:block;min-height:150px;width:80px!important;height:100%;margin:0px auto;object-fit: contain;";
                callBack(e)
            })
    }
    else {
        e.target.src = NO_CAMERA_IMAGE;
        e.target.style = "display:block;min-height:150px;width:80px!important;height:100%;margin:0px auto;object-fit: contain;";
        callBack(e)
    }
    
}


