export const isPostAvailable=(id)=>{
    var postId = window.location.href.split('#') ? window.location.href.split('#')[1]:'';
    return postId==id ? true : false;
}

export const removeTransFromCss=(act)=>{
if(act=='add'){
   var iscont = document.querySelector('.page-container') ? document.querySelector('.page-container').style.transform ='none':'';
}
else if(act=='remove'){
   var removeCont = document.querySelector('.page-container') ? document.querySelector('.page-container').removeAttribute("style"):'';
}
else{}
}

export const findFromArray = (data, id, index) => {
    if (data && data.length > 0) {
        return data.findIndex((checkItem) => checkItem.id == id);
    }
    else return index;
} 

export const loadImage = async src =>
    new Promise((resolve, reject) => {
        const img = new Image();
        img.onload = () => resolve(img);
        img.onerror = (...args) => reject(args);
        img.src = src;
    });



    export const ShopableText = ({ ugc_products }) => {
        const productsData = convertObjectToArray(ugc_products);
        let alternativeText = "";
        if (productsData && productsData.length > 0 && productsData[0] && productsData[0].UgcProduct && Object.keys(productsData[0].UgcProduct).length > 0 && productsData[0].UgcProduct && productsData[0].UgcProduct.product_title) {
            alternativeText = productsData[0].UgcProduct.product_title;
            if (productsData[0].UgcProduct.product_discount && productsData[0].UgcProduct.product_discount > 0) {
                alternativeText = `${alternativeText}   ${productsData[0].UgcProduct.price_currency_symbol} ${productsData[0].UgcProduct.product_discount.toFixed(2)}`
            }
            else if (productsData[0].UgcProduct.product_price && productsData[0].UgcProduct.product_price > 0) {
                alternativeText = `${alternativeText}  ${productsData[0].UgcProduct.price_currency_symbol} ${productsData[0].UgcProduct.product_price.toFixed(2)}`
            }
        }
        return <div className="ts_custom-shopable-text">{alternativeText} </div>
    }
    


