
import axios from 'axios';
import { API_URL } from './api'
import queryString from 'query-string'


localStorage.setItem('source_tagshop','wall')
 
var isxSocialwall = document.querySelector('.taggbox-socialwall') ? document.querySelector('.taggbox-socialwall') : document.querySelector('.tagshop-socialwall');

var istaggs = isxSocialwall && isxSocialwall.getAttribute('data-tags') ? isxSocialwall.getAttribute('data-tags') : false;
var keywords = isxSocialwall && isxSocialwall.getAttribute('data-keywords') ? isxSocialwall.getAttribute('data-keywords') : false;
var themeid = isxSocialwall && isxSocialwall.getAttribute('data-theme') ? isxSocialwall.getAttribute('data-theme') : '';
export var source = isxSocialwall && isxSocialwall.getAttribute('data-source') ? isxSocialwall.getAttribute('data-source') : '';

 

var checkTags = {}
if (istaggs == "true") {
    checkTags = { tag: 1 }
    if (keywords) checkTags = { ...checkTags, keywords }
}
else {
    const parsed = queryString.parse(window.location.search);
    if (parsed.tags) {
        const tags = parsed.tags;
        checkTags = { tag: tags.includes("true") ? 1 : 0 }
    }
}

const requestInterceptor = (config) => {
    let headers = {
        'authorization': 'Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9eyJpZCI6MTIzNDUsInUiOiJnZWlyOTE4MjkzIiwiaWF0IjoxNjM3NjU2OTYwLCJleHAiOjE2NDU0MzI5NjB9W08RIyF40I9wIDxTaAZ1Zlg=',
        'productDomain': 'taggshop.io',
        ...config.headers,
        themeid,
        ...checkTags
    };

    if (source == 'website') {
        headers['website'] = '1';
        localStorage.setItem('source_tagshop','website')
    }
    if ( (window.location.pathname.includes("/w/") && window.location.href.includes("taggshop.io/")) || window.location.href.includes("localhost:3") || (window.location.pathname.includes("/w/") && window.location.href.includes("tagshop.ai/"))) {
        headers['website'] = '1';
        localStorage.setItem('source_tagshop','website')
    }
 
    return {
        ...config,
        headers
    };
};

class HttpClient {
    constructor() {
        const options = { baseURL: API_URL };
        const instance = axios.create(options);
        instance.interceptors.request.use(requestInterceptor);
        axios.interceptors.response.use(
            function (response) {
                return response;
            },
            function (error) {
                return Promise.reject(error);
            }
        );
        return instance;
    }
}
export default HttpClient;
