import { SHOW_POP_UP, CLOSE_POP_UP, SHOW_HOTSPOT, SET_MEDIA_SIZE, SHOW_REEL_POP_UP, CLOSE_REEL_POP_UP, APPEND_POP_UP_DATA } from './reducersKeys'
const initialState = { isShowPopUp: false, data: null, showHotspot: null, index: null, showAll: false, hotspotId: null, mediaHeight: 0, mediaWidth: 0, isShowReelPopUp: false }
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case SHOW_POP_UP: {
            return { ...state, isShowPopUp: true, data: action.payload }
        }
        case SHOW_REEL_POP_UP: {
            return { ...state, isShowReelPopUp: true, data: action.payload }
        }
        case CLOSE_POP_UP: {
            return { ...state, isShowPopUp: false, data: null }
        }
        case CLOSE_REEL_POP_UP: {
            return { ...state, isShowReelPopUp: false, data: null }
        }
        case SHOW_HOTSPOT: {
            return { ...state, ...action.payload}
        }
        case SET_MEDIA_SIZE: {
            return { ...state, ...action.payload}
        }
        case APPEND_POP_UP_DATA: {
            // console.log('action.payload', state.idArray, action.payload)
            return { ...state, data: {...state.data, idArray: [...state.data.idArray, ...action.payload], index: state.data.idArray.length - 1 }}

        }
        default:
            return { ...state }
    }

}